import { mapGetters } from 'vuex';
import { generateUrl } from '@nextcloud/router';
import { NcActionLink } from '@nextcloud/vue';
export default {
  name: 'ActionDownload',
  components: {
    NcActionLink
  },
  props: {
    title: {
      type: String,
      required: true
    },
    selectedFileIds: {
      type: Array,
      required: true
    }
  },
  computed: { ...mapGetters(['files']),

    downloadUrl() {
      const params = new URLSearchParams();
      params.append('files', JSON.stringify(this.fileNames));
      return generateUrl(`/apps/files/ajax/download.php?${params}`);
    },

    fileNames() {
      return this.selectedFileIds.map(fileId => this.files[fileId].filename);
    }

  }
};